@import "../../global";
.plans-container{
    margin-bottom: 6vw;
    margin-top: 2vwn;
padding: 0 2rem;
display: flex;
flex-direction: column;
gap:4rem;
position: relative;
margin-top: 2rem;
a{
    color:black;
}
}
.plans{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem ;
}
.plan{
    display: flex;
    flex-direction:column;
    background:#2a3241;
  
    color: white;
    gap: 2rem;
    padding: 1.5rem;
    width: 18rem;
    border :white 2px solid;
    box-shadow: 3px 5px 9px rgb(145, 142, 142);
    
}
.plan:nth-child(2){
    background: $planCard;
    transform: scale(1.1);
}
.plan>svg{
    fill: $orange;
    width: 2rem;
    height: 2rem;

}
.plan>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;


}
.plan>:nth-child(3){
    font-size: 3rem;
    font-weight: bold;


}
.plan>:nth-child(5){
    font-size: 0.8rem;

}
.features{
    display: flex;
    flex-direction: column;
    gap: 1rem;

}
.feature{
    display: flex;
    align-items: center;
    gap: 1rem;
img{
    width: 1rem;
}
}
.plans>:nth-child(2)>svg{
    fill: white;
}
.plans>:nth-child(2)>button{
    color: rgb(64, 142, 243);
    cursor: pointer;
}
.plans>:nth-child(1)>button,.plans>:nth-child(3)>button{
    cursor: pointer;
}
.plans-blur-1{
    width: 32rem;
    height: 23rem;
    top: 6rem;
    left: 0rem;

}
.plans-blur-2{
    width: 32rem;
    height: 23rem;
    top: 10rem;
    right: 0rem;
    
}

@media screen and (max-width:768px){
  .h{
display: flex;
flex-direction: column;
}
.plans-container{
    margin-top:20px;
    gap: 2rem;
}
.plan:nth-child(2) {
    
    transform: none;
}
.plan{
    border :white 2px solid;
    box-shadow: 3px 5px 5px white;
    gap: 1rem;
}
}

